"use client";

import { useEffect, useState } from "react";
import { supabase } from "@/lib/supabase";
import { useRouter } from "next/navigation";
import { 
  BadgeCheck, Plus, Package, Loader2, Settings, Edit2, 
  Trash2, Eye, Box, CreditCard, Activity, FolderOpen,
  LayoutList, ShoppingBag, ExternalLink, Share2, Search
} from "lucide-react";
import Image from "next/image";
import { Store, Product } from "@/types";

// --- IMPORT ALL MODALS & MANAGERS ---
import AddProductModal from "@/components/store/AddProductModal";
import EditProfileModal from "@/components/store/EditProfileModal";
import EditProductModal from "@/components/store/EditProductModal";
import ManageCategoriesModal from "@/components/store/ManageCategoriesModal";
import OrdersManager from "@/components/store/OrdersManager"; 

export default function Dashboard() {
  const router = useRouter();
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState<'inventory' | 'orders'>('inventory');
  
  // Data State
  const [store, setStore] = useState<Store | null>(null);
  const [products, setProducts] = useState<Product[]>([]);
  const [revenue, setRevenue] = useState(0); 
  
  // UI State
  const [inventorySearch, setInventorySearch] = useState(""); // <--- Search State
  
  // Modal States
  const [isAddProductOpen, setIsAddProductOpen] = useState(false);
  const [isEditProfileOpen, setIsEditProfileOpen] = useState(false);
  const [isCategoriesOpen, setIsCategoriesOpen] = useState(false);
  const [editingProduct, setEditingProduct] = useState<Product | null>(null);

  // --- LOAD DATA ---
  const loadDashboardData = async () => {
    const { data: { user } } = await supabase.auth.getUser();
    if (!user) { router.push("/login"); return; }

    // 1. Get Store
    const { data: userStore } = await supabase.from("stores").select("*").eq("owner_id", user.id).single();
    if (!userStore) { router.push("/onboarding"); return; }
    setStore(userStore);

    // 2. Get Products
    const { data: storeProducts } = await supabase
      .from("products")
      .select("*")
      .eq("store_id", userStore.id)
      .order('created_at', { ascending: false });
    setProducts(storeProducts || []);

    // 3. CALCULATE REVENUE (Only Completed Orders)
    const { data: orders } = await supabase
      .from("orders")
      .select("total_amount, status")
      .eq("store_id", userStore.id);
    
    const totalRev = orders
      ?.filter(o => o.status === 'completed') // ONLY COMPLETED!
      .reduce((sum, o) => sum + Number(o.total_amount), 0) || 0;
      
    setRevenue(totalRev);
    setLoading(false);
  };

  useEffect(() => { loadDashboardData(); }, [router]);

  // --- ACTIONS ---
  const deleteProduct = async (id: string) => {
    if(!confirm("Are you sure? This action cannot be undone.")) return;
    await supabase.from("products").delete().eq("id", id);
    loadDashboardData();
  };

  const copyStoreLink = () => {
    if (!store) return;
    const url = `${window.location.origin}/${store.slug}`;
    navigator.clipboard.writeText(url);
    alert("Store link copied to clipboard! ðŸš€");
  };

  // --- FILTER INVENTORY ---
  const filteredInventory = products.filter(p => 
    p.name.toLowerCase().includes(inventorySearch.toLowerCase())
  );

  if (loading) return <div className="min-h-screen flex items-center justify-center bg-gray-50"><Loader2 className="animate-spin text-gray-400" /></div>;
  if (!store) return null;

  return (
    <div className="min-h-screen bg-gray-50 pb-32 font-sans">
      
      {/* --- TOP BAR --- */}
      <header className="bg-white border-b border-gray-200 sticky top-0 z-20">
        <div className="max-w-6xl mx-auto px-4 h-16 flex items-center justify-between">
          <div className="font-bold text-lg md:text-xl tracking-tight flex items-center gap-2 text-gray-900">
            StoreLink <span className="bg-gray-900 text-white text-[10px] px-2 py-0.5 rounded-full font-bold uppercase tracking-wider">HQ</span>
          </div>
          
          <div className="flex items-center gap-2">
             {/* Share Button */}
             <button 
               onClick={copyStoreLink}
               className="flex items-center gap-2 bg-emerald-50 text-emerald-700 px-3 py-2 rounded-xl hover:bg-emerald-100 transition border border-emerald-100"
             >
                <Share2 size={18} /> <span className="hidden md:inline text-sm font-bold">Share Shop</span>
             </button>

             {/* Categories */}
             <button onClick={() => setIsCategoriesOpen(true)} className="flex items-center gap-2 bg-gray-100 text-gray-700 px-3 py-2 rounded-xl hover:bg-gray-200 transition">
                <FolderOpen size={18} /> <span className="hidden md:inline text-sm font-bold">Categories</span>
             </button>

             {/* Settings */}
             <button onClick={() => setIsEditProfileOpen(true)} className="flex items-center gap-2 bg-gray-100 text-gray-700 px-3 py-2 rounded-xl hover:bg-gray-200 transition">
                <Settings size={18} /> <span className="hidden md:inline text-sm font-bold">Settings</span>
             </button>
          </div>
        </div>
      </header>

      <main className="max-w-6xl mx-auto px-4 py-6 md:py-8">
        
        {/* --- EMPIRE HEADER CARD --- */}
        <div className="bg-gray-900 rounded-3xl p-6 md:p-8 shadow-xl mb-8 relative overflow-hidden group">
          <div className="absolute top-0 right-0 w-64 h-64 bg-gradient-to-br from-gray-800 to-black rounded-full blur-3xl -translate-y-1/2 translate-x-1/2 opacity-50 pointer-events-none"></div>
          <div className="absolute bottom-0 left-0 w-48 h-48 bg-blue-900 rounded-full blur-3xl translate-y-1/2 -translate-x-1/2 opacity-30 pointer-events-none"></div>
          
          <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-6 relative z-10">
             <div className="flex items-center gap-4">
                <div className="w-16 h-16 md:w-20 md:h-20 rounded-2xl bg-white p-1 shadow-lg shrink-0">
                  <div className="w-full h-full relative rounded-xl overflow-hidden bg-gray-100">
                    {store.logo_url ? <Image src={store.logo_url} alt="Logo" fill className="object-cover" /> : <div className="w-full h-full flex items-center justify-center text-gray-400 font-bold text-2xl">{store.name[0]}</div>}
                  </div>
                </div>
                
                <div>
                  <p className="text-gray-400 text-[10px] font-bold uppercase tracking-widest mb-1">Command Center</p>
                  <h1 className="text-2xl md:text-4xl font-extrabold text-white flex items-center gap-2 leading-tight">
                    {store.name}
                    {store.subscription_plan === 'premium' && <BadgeCheck className="text-blue-400 w-5 h-5 md:w-6 md:h-6 shrink-0" />}
                  </h1>
                  <p className="text-gray-400 text-xs md:text-sm mt-1 flex items-center gap-1">
                     {store.location} â€¢ Online
                  </p>
                </div>
             </div>
             
             <div className="grid grid-cols-2 gap-3 w-full md:w-auto">
               <a href={`/${store.slug}`} target="_blank" className="text-center px-4 py-3 bg-gray-800 border border-gray-700 text-white rounded-xl text-xs md:text-sm font-bold hover:bg-gray-700 transition flex items-center justify-center gap-2">
                 Visit Shop <ExternalLink size={14} />
               </a>
               <button onClick={() => setIsAddProductOpen(true)} className="px-4 py-3 bg-white text-gray-900 rounded-xl text-xs md:text-sm font-bold shadow-lg hover:bg-gray-100 transition flex items-center justify-center gap-2">
                 <Plus size={16} /> New Item
               </button>
             </div>
          </div>
        </div>

        {/* --- PERFORMANCE GRID --- */}
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-3 md:gap-4 mb-8">
           {/* Revenue */}
           <div className="bg-white p-4 md:p-6 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-28 md:h-36 relative overflow-hidden">
              <div className="absolute top-0 right-0 w-12 h-12 bg-emerald-50 rounded-bl-2xl -mr-0 -mt-0 z-0"></div>
              <div className="flex justify-between items-start relative z-10">
                 <div className="p-2 bg-emerald-50 rounded-xl text-emerald-600"><CreditCard size={18} className="md:w-6 md:h-6" /></div>
                 <span className="text-[10px] text-gray-400 font-extrabold tracking-widest mt-1">REVENUE</span>
              </div>
              <p className="text-xl md:text-3xl font-extrabold text-gray-900 truncate relative z-10 mt-2">
                â‚¦{revenue.toLocaleString()}
              </p>
           </div>

           {/* Products */}
           <div className="bg-white p-4 md:p-6 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-28 md:h-36">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-purple-50 rounded-lg text-purple-600"><Box size={18} className="md:w-6 md:h-6" /></div>
                 <span className="text-[10px] text-gray-400 font-extrabold tracking-widest mt-1">ITEMS</span>
              </div>
              <p className="text-2xl md:text-3xl font-extrabold text-gray-900 mt-2">{products.length}</p>
           </div>

           {/* Views (REAL) */}
           <div className="bg-white p-4 md:p-6 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-28 md:h-36">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-orange-50 rounded-lg text-orange-600"><Eye size={18} className="md:w-6 md:h-6" /></div>
                 <span className="text-[10px] text-gray-400 font-extrabold tracking-widest mt-1">VIEWS</span>
              </div>
              <p className="text-2xl md:text-3xl font-extrabold text-gray-900 mt-2">
                {store.view_count.toLocaleString()}
              </p>
           </div>

           {/* Status */}
           <div className="bg-white p-4 md:p-6 rounded-2xl shadow-sm border border-gray-100 flex flex-col justify-between h-28 md:h-36">
              <div className="flex justify-between items-start">
                 <div className="p-2 bg-blue-50 rounded-lg text-blue-600"><Activity size={18} className="md:w-6 md:h-6" /></div>
                 <span className="text-[10px] text-gray-400 font-extrabold tracking-widest mt-1">HEALTH</span>
              </div>
              <p className="text-lg md:text-2xl font-extrabold text-green-600 mt-2">Active</p>
           </div>
        </div>

        {/* --- TABS (Scrollable) --- */}
        <div className="flex gap-6 mb-6 border-b border-gray-200 overflow-x-auto scrollbar-hide">
          <button 
            onClick={() => setActiveTab('inventory')}
            className={`pb-4 px-2 text-sm font-bold transition flex items-center gap-2 whitespace-nowrap ${activeTab === 'inventory' ? 'text-gray-900 border-b-2 border-gray-900' : 'text-gray-400 hover:text-gray-600'}`}
          >
            <LayoutList size={18}/> Inventory
          </button>
          <button 
            onClick={() => setActiveTab('orders')}
            className={`pb-4 px-2 text-sm font-bold transition flex items-center gap-2 whitespace-nowrap ${activeTab === 'orders' ? 'text-gray-900 border-b-2 border-gray-900' : 'text-gray-400 hover:text-gray-600'}`}
          >
            <ShoppingBag size={18}/> Orders
          </button>
        </div>
        
        {/* --- MAIN CONTENT AREA --- */}
        {activeTab === 'inventory' ? (
          <div>
            
            {/* INVENTORY SEARCH BAR (NEW!) */}
            {products.length > 0 && (
              <div className="relative mb-6">
                <Search className="absolute left-3 top-3 text-gray-400 w-5 h-5" />
                <input 
                  type="text" 
                  placeholder="Search your inventory..."
                  className="w-full bg-white pl-10 pr-4 py-3 rounded-xl border border-gray-200 focus:outline-none focus:ring-2 focus:ring-gray-900 shadow-sm transition"
                  value={inventorySearch}
                  onChange={(e) => setInventorySearch(e.target.value)}
                />
              </div>
            )}

            {/* PRODUCT LIST */}
            {products.length > 0 ? (
              <div className="bg-white rounded-2xl border border-gray-100 overflow-hidden shadow-sm">
                {filteredInventory.length > 0 ? (
                  filteredInventory.map((product, index) => (
                    <div key={product.id} className={`flex items-center gap-4 p-4 hover:bg-gray-50 transition relative ${index !== filteredInventory.length - 1 ? 'border-b border-gray-50' : ''}`}>
                      
                      {/* Image */}
                      <div className="h-16 w-16 md:h-20 md:w-20 bg-gray-100 rounded-xl relative overflow-hidden flex-shrink-0 border border-gray-200">
                         {product.image_urls && product.image_urls.length > 0 ? <Image src={product.image_urls[0]} alt="" fill className="object-cover" /> : <div className="w-full h-full flex items-center justify-center text-gray-300"><Package size={20} /></div>}
                      </div>
                      
                      {/* Info */}
                      <div className="flex-1 min-w-0 pr-8">
                         <h3 className="font-bold text-gray-900 text-sm truncate">{product.name}</h3>
                         <div className="flex flex-wrap items-center gap-2 mt-1">
                           <p className="text-emerald-600 font-bold text-sm">â‚¦{product.price.toLocaleString()}</p>
                           <span className="text-gray-300 hidden md:inline">â€¢</span>
                           {product.stock_quantity > 0 ? (
                              <span className="text-[10px] bg-green-50 text-green-700 px-1.5 py-0.5 rounded-md font-bold">Stock: {product.stock_quantity}</span>
                           ) : (
                              <span className="text-[10px] bg-red-50 text-red-600 px-1.5 py-0.5 rounded-md font-bold">Sold Out</span>
                           )}
                         </div>
                      </div>

                      {/* Actions */}
                      <div className="flex flex-col md:flex-row gap-2 md:relative">
                         <button onClick={() => setEditingProduct(product)} className="p-2 bg-gray-50 text-gray-600 rounded-lg hover:bg-blue-50 hover:text-blue-600 transition">
                            <Edit2 size={16} />
                         </button>
                         <button onClick={() => deleteProduct(product.id)} className="p-2 bg-gray-50 text-gray-400 rounded-lg hover:bg-red-50 hover:text-red-500 transition">
                            <Trash2 size={16} />
                         </button>
                      </div>
                    </div>
                  ))
                ) : (
                  <div className="p-8 text-center text-gray-500">
                    No products found matching "{inventorySearch}"
                  </div>
                )}
              </div>
            ) : (
              // Empty State
              <div className="bg-white rounded-3xl border border-dashed border-gray-200 p-8 md:p-12 text-center flex flex-col items-center">
                 <div className="w-16 h-16 bg-gray-50 rounded-full flex items-center justify-center mb-4"><Package className="text-gray-300 w-8 h-8" /></div>
                 <h3 className="text-lg font-bold text-gray-900">Empty Shelf</h3>
                 <p className="text-gray-500 text-sm max-w-xs mx-auto mt-2 mb-6">Add your first item to start building your empire.</p>
                 <button onClick={() => setIsAddProductOpen(true)} className="px-6 py-3 bg-gray-900 text-white rounded-xl text-sm font-bold shadow-lg hover:bg-gray-800 transition w-full md:w-auto">Add Product</button>
              </div>
            )}
          </div>
        ) : (
          /* --- ORDER LIST --- */
          <OrdersManager storeId={store.id} onUpdate={loadDashboardData} />
        )}

      </main>

      {/* --- MODALS --- */}
      <AddProductModal storeId={store.id} isOpen={isAddProductOpen} onClose={() => setIsAddProductOpen(false)} onSuccess={loadDashboardData} />
      <EditProfileModal store={store} isOpen={isEditProfileOpen} onClose={() => setIsEditProfileOpen(false)} onSuccess={loadDashboardData} />
      <ManageCategoriesModal storeId={store.id} isOpen={isCategoriesOpen} onClose={() => setIsCategoriesOpen(false)} />
      <EditProductModal product={editingProduct} isOpen={!!editingProduct} onClose={() => setEditingProduct(null)} onSuccess={loadDashboardData} />

    </div>
  );
}